.news__text{font-size:24px;line-height:1.375;color:#A4A3BA}.news__item-title{font-size:45px;line-height:1.26667;font-weight:700;color:#3C3B4E;font-family:"Rubik",sans-serif}.news__date{border-radius:100px;font-size:24px;line-height:1.25;color:#A4A3BA;font-weight:700}.news__loader{position:relative;margin-top:50px;display:flex;justify-content:center}.news__loader .dot{width:24px;height:24px;border-radius:50%;background:#33aa7e;display:inline-block;-webkit-animation:slide 1s infinite;animation:slide 1s infinite}.dot:nth-child(1){-webkit-animation-delay:0.1s;animation-delay:0.1s;background:Navy(#33aa7e, 50)}.dot:nth-child(2){-webkit-animation-delay:0.2s;animation-delay:0.2s;background:Navy(#33aa7e, 100)}.dot:nth-child(3){-webkit-animation-delay:0.3s;animation-delay:0.3s;background:Navy(#33aa7e, 150)}.dot:nth-child(4){-webkit-animation-delay:0.4s;animation-delay:0.4s;background:Navy(#33aa7e, 200)}.dot:nth-child(5){-webkit-animation-delay:0.5s;animation-delay:0.5s;background:Navy(#33aa7e, 250)}@-webkit-keyframes slide{0%{transform:scale(1)}50%{opacity:.3;transform:scale(2)}100%{transform:scale(1)}}@keyframes slide{0%{transform:scale(1)}50%{opacity:.3;transform:scale(2)}100%{transform:scale(1)}}

